import * as XLSX from "xlsx";
import config from "./config.json";

// Add this utility function at the top of the file
const processDataInChunks = (data, chunkSize = 1000) => {
  const chunks = [];
  for (let i = 0; i < data.length; i += chunkSize) {
    chunks.push(data.slice(i, i + chunkSize));
  }
  return chunks;
};

const validateFile = (file, collabSpaceTitles, selectedOperation) => {
  return new Promise((resolve, reject) => {
    console.log("Starting file validation for:", file.name, selectedOperation);
    console.log("Selected Operation:", selectedOperation);
    console.log("File Name:", file.name);
    console.log(
      "[validateFile.js] Required Collaborative Space:",
      collabSpaceTitles
    );

    const operationNames = {
      1: "Physical Product",
      2: "Physical Product Structure",
      3: "Document",
      4: "Physical Product-Document",
    };

    // Get matched operation name
    const matchedOperation = operationNames[selectedOperation];
    console.log("Matched Operation:", matchedOperation);

    // Get mandatory attributes from config based on operation
    const mandatoryAttributes =
      config.MassUpload[matchedOperation]?.mandatoryAttributes || [];
    console.log("Mandatory Attributes:", mandatoryAttributes);

    if (!selectedOperation) {
      reject({
        errors: ["Please select an operation first."],
        headers: [],
      });
      return;
    }

    // üîç Ensure collabSpaceTitles is an array
    if (!Array.isArray(collabSpaceTitles)) {
      console.error(
        "[validateFile.js] ‚ùå collabSpaceTitles is not an array!",
        collabSpaceTitles
      );
      reject({
        errors: ["Internal error: Invalid Collaborative Space list."],
        headers: [],
      });
      return;
    }

    console.log(
      "[validateFile.js] Required Collaborative Spaces:",
      collabSpaceTitles
    );

    const reader = new FileReader();
    reader.readAsBinaryString(file);

    reader.onload = async (e) => {
      console.log("File successfully read. Parsing...");

      const workbook = XLSX.read(e.target.result, { type: "binary" });
      const sheetName = workbook.SheetNames[0];
      console.log("Detected sheet name:", sheetName);

      const worksheet = workbook.Sheets[sheetName];

      const headers =
        XLSX.utils.sheet_to_json(worksheet, { header: 1 })[0] || [];
      console.log("Extracted headers:", headers);

      if (!headers || headers.length === 0) {
        console.error("‚ùå No headers found in the file.");
        reject({
          errors: ["The uploaded file has no content or is empty."],
          headers: [],
        });
        return;
      }

      // ‚úÖ Convert worksheet into JSON (with empty cell values captured)
      const allSheetData = XLSX.utils.sheet_to_json(worksheet, { defval: "" });
      console.log("Extracted all sheet data:", allSheetData);
      console.log(`Total rows in sheet: ${allSheetData.length}`);

      // Add validation for empty sheet (only headers, no data)
      if (allSheetData.length === 0) {
        console.error("‚ùå File contains headers but no data rows.");
        reject({
          errors: ["The uploaded file contains headers but no data rows."],
          headers,
        });
        return;
      }

      // Process data in chunks
      const chunks = processDataInChunks(allSheetData, 1000);
      console.log(`Split data into ${chunks.length} chunks of 1000 rows each`);

      const validationErrors = [];
      const invalidPrefixes = ["MMI-", "RS-", "DAN-", "RSC-", "TF-", "ROXA-"];

      // Process each chunk
      for (let chunkIndex = 0; chunkIndex < chunks.length; chunkIndex++) {
        const chunk = chunks[chunkIndex];
        console.log(
          `Processing chunk ${chunkIndex + 1} of ${chunks.length} (${
            chunk.length
          } rows)`
        );

        // Process each row in the chunk
        chunk.forEach((row, indexInChunk) => {
          const globalRowIndex = chunkIndex * 1000 + indexInChunk;

          // Validate mandatory fields
          mandatoryAttributes.forEach((field) => {
            if (!row[field] || row[field].toString().trim() === "") {
              validationErrors.push(
                `Row ${globalRowIndex + 2}: "${field}" is required but is empty`
              );
            }
          });

          // Rest of your existing validation logic, but use globalRowIndex + 2 instead of index + 2
          // üîç **Step 5: Validate "Collaborative Space" Field**//
          if (
            matchedOperation === "Physical Product" ||
            matchedOperation === "Document"
          ) {
            const userCollabSpace = row["Collaborative Space"]
              ? row["Collaborative Space"].trim()
              : "";
            console.log(
              `[validateFile.js] Row ${
                globalRowIndex + 2
              }: User's Collaborative Space:`,
              userCollabSpace
            );

            if (!userCollabSpace) {
              validationErrors.push(
                `Row ${
                  globalRowIndex + 2
                }: "Collaborative Space" is Missing or Empty`
              );
            } else if (!collabSpaceTitles.includes(userCollabSpace)) {
              validationErrors.push(
                `Row ${
                  globalRowIndex + 2
                }: You are not allowed to create in "Collaborative Space -" (${userCollabSpace})`
              );
            }
          }
          // **üîç EIN Number / Document Name Validation based on matchedOperation**
          let valueToValidate = null;
          let columnName = "";

          if (
            matchedOperation === "Physical Product" ||
            matchedOperation === "Physical Product Structure"
          ) {
            columnName = "EIN Number";
            valueToValidate = row[columnName]
              ? row[columnName].toString().trim()
              : null;
            console.log(
              `[validateFile.js] Row ${
                globalRowIndex + 2
              }: Checking EIN Number:`,
              valueToValidate
            );
          } else if (
            matchedOperation === "Physical Product-Document" ||
            matchedOperation === "Document"
          ) {
            columnName = "Document Name";
            valueToValidate = row[columnName]
              ? row[columnName].toString().trim()
              : null;
            console.log(
              `[validateFile.js] Row ${
                globalRowIndex + 2
              }: Checking Document Name:`,
              valueToValidate
            );
          }

          if (valueToValidate) {
            const hasInvalidPrefix = invalidPrefixes.some((prefix) =>
              valueToValidate.startsWith(prefix)
            );
            if (hasInvalidPrefix) {
              console.error(
                `[validateFile.js] ‚ùå Invalid prefix found in ${valueToValidate}`
              );
              validationErrors.push(
                `Row ${
                  globalRowIndex + 2
                }: "${columnName}" value "${valueToValidate}" has an invalid prefix.`
              );
            }
          } else {
            console.warn(
              `[validateFile.js] üö® Row ${
                globalRowIndex + 2
              }: ${columnName} is missing.`
            );
          }
        });

        console.log(`Completed chunk ${chunkIndex + 1} validation`);
      }

      // After processing all chunks
      if (validationErrors.length > 0) {
        console.log(
          `Found ${validationErrors.length} validation errors across all chunks`
        );
        reject({
          errors: validationErrors,
          headers,
          mandatoryAttributes,
        });
        return;
      }

      // If validation successful
      console.log(
        "‚úÖ File validation successful. No errors found in any chunk."
      );
      resolve({
        headers,
        sheetData: allSheetData, // Still return complete dataset
        validationErrors: [],
        mandatoryAttributes,
      });
    };

    // Handle File Read Errors
    reader.onerror = () => {
      console.error("‚ùå Error reading the file.");
      reject({ errors: ["File reading failed."], headers: [] });
    };
  });
};

export default validateFile;
